<template>
    <h2>{{ roundNo }}. Runde, {{ cards }} Karte(n)</h2>
    <form v-if="!betsPlaced" class="game-round-form" @submit.prevent="finishBets">
      <h3>Stiche ansagen</h3>
      <div class="player-row" v-for="(player, index) in players" :key="index">
        <label :for="'player-bets-' + index">{{ player.name }}:</label>
        <input :id="'player-bets-' + index" type="number" v-model="player.bets">
        <button class="increase-btn" @click.prevent="player.bets++">
          <i class="material-icons">add</i>
        </button>
      </div>
      <button class="btn submit-btn" type="submit">Runde Starten</button>
    </form>
    <form v-else class="game-round-form" @submit.prevent="finishRound">
      <h3>Gemachte Stiche</h3>
      <div class="player-row" v-for="(player, index) in players" :key="index">
        <label :for="'player-wins-' + index">{{ player.name }}:</label>
        <input :id="'player-wins-' + index" type="number" v-model="player.wins">
        <button class="increase-btn" @click.prevent="player.wins++">
          <i class="material-icons">add</i>
        </button>
      </div>
      <button class="btn submit-btn" type="submit">Nächste Runde</button>
    </form>       
</template>

<script>
export default {
  name: 'GameRound',
  props: {
    players: Array,
    roundNo: Number,
    maxCards: Number
  },
  data() {
    return {
        cards: Number,
        betsPlaced: false
    }
  },
  created : function(){
    this.resetPoints();
  },
  watch: {
    roundNo : function(newRoundNo) {
      if(newRoundNo == 1){
        this.resetPoints();
      }
      this.cards = this.maxCards - Math.abs(((this.roundNo - 1 - (2*(this.maxCards-1)*Math.floor((this.roundNo-2)/2/(this.maxCards-1)))) % (2*(this.maxCards)-1)) - this.maxCards+1)
      this.betsPlaced = false;
    }
  },
  methods: {
    resetPoints(){
      this.players.forEach(p => {
            p.bets = 0;
            p.wins = 0;
            p.points = 0;
    })
    },
    finishBets(){
      console.log(this.players)
      let bets = 0;
      this.players.forEach(p => {
        bets += p.bets;
      });
      if(bets == this.cards){
        alert("Ungültige Anzahl angesagter Stiche");
        return;
      }
      this.betsPlaced = true;
    },
    finishRound(){
      let wins = 0;
      this.players.forEach(p => {wins += p.wins});
      if(wins != this.cards){
        alert("Es sind zu viele/zu wenige Stiche eingetragen");
        return;
      }
      let round = {
        'players': this.players,
        'roundNo': this.roundNo
      }
      this.$emit('finish-round', round);
    }
  }
}
</script>

<style scoped>
.game-round-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5px;
}

.player-row {
  display: flex;
  align-items: center;
  margin: 10px 0;
}

label {
  width: 80px;
}

input {
  width: 60px;
  margin: 0 10px;
}

.submit-btn {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: #4caf50;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submit-btn:hover {
  background-color: #5fbd64;
}

.increase-btn {
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
}
@media (max-width: 768px) {
  .game-round-form {
    align-items: stretch;
  }

  label {
    font-size: 20px;
    text-align: left;
  }
  
  input {
    width: 80%;

    font-size: 20px;
  }
  
  button {
    font-size: 16px;
  }
}
</style>